:root, button, input {
	font-size: 18px;
}

body {
	background: #f3f3f3;
	width: 80%;
	margin: 50px auto ;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
	"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
	sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

hr {
	margin: 50px auto;
	max-width: 150px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.excercise {
	position: relative;
	padding: 30px;
	background: #fff;
	box-shadow: 0 5px 10px rgba(0,0,0,0.15);
}

.late:after {
	position: absolute;
	top: -10px;
	right: -10px;
	content: "Late Addition";
	color: white;
	border-radius: 50px;
	padding: 5px 15px;
	background: #CC3300;
}

h2 {
	margin-top: 0;
}
button {
	padding: 10px 15px;
	border-radius: 8px;
	border: 0;
	color: white;
	background: #444;
}

button:last-child, input[type="submit"] {
	margin-left: 15px;
}
form {
	margin-bottom: 20px; 
}
input[type="submit"] {
	padding: 10px 15px;
	border-radius: 8px;
	border: 0;
	color: white;
	background: #444444;
}
input {
	padding: 10px 15px;
	border-radius: 8px;
	border: 1px solid grey;
}
.calculator .wrapper {
	display: grid;
	grid-template-columns: repeat( 4, 1fr );
	grid-gap: 10px;
	max-width: 230px;
	background: #f3f3f3;
	padding: 25px;
}

.calculator h3 {
	grid-column: span 4;
	grid-row: 1;
	background: #e6e6e6;
	height: 50px;
	margin: 0;
	border-radius: 50px;
	text-align: right;
	line-height: 50px;
	padding: 0 20px;
	overflow: scroll;
}

.calculator .tool {
	grid-column: 4;
	background: orange;
	color: white;
}

.calculator input {
	background: #444444;
	color: white;
	border-radius: 50px;
	border: 0;
	height: 50px;
	width: 50px;
}

.calculator input[value="9"] {
	grid-row: 3;
	grid-column: 3;
}

.calculator input[value="8"] {
	grid-row: 3;
	grid-column: 2;
}

.calculator input[value="7"] {
	grid-row: 3;
	grid-column: 1;
}

.calculator input[value="6"] {
	grid-row: 4;
	grid-column: 3;
}

.calculator input[value="5"] {
	grid-row: 4;
	grid-column: 2;
}

.calculator input[value="4"] {
	grid-row: 4;
	grid-column: 1;
}

.calculator input[value="3"] {
	grid-row: 5;
	grid-column: 3;
}

.calculator input[value="2"] {
	grid-row: 5;
	grid-column: 2;
}

.calculator input[value="1"] {
	grid-row: 5;
	grid-column: 1;
}

.calculator input[value="0"] {
	grid-row: 6;
	grid-column: 1/4;
	width: auto;
}

.calculator input[value="+"] {
	grid-row: 5;
}

.calculator input[value="-"] {
	grid-row: 4;
}

.calculator input[value="*"] {
	grid-row: 3;
}

.calculator input[value="/"] {
	grid-row: 2;
}

.calculator input[value="="] {
	grid-row: 6;
}

.calculator input[value="%"] {
	grid-row: 2;
	grid-column: 3;
	background: #cccccc;
	color: black;
}

.calculator input[value="AC"] {
	grid-row: 2;
	grid-column: 1 / 3;
	background: #cccccc;
	color: black;
	width: auto;
}

